import Vue from 'vue'
import VueRouter from 'vue-router'
import Landing from '@/views/Landing.vue'
import MicroFreak from '@/views/MicroFreak.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Landing
  },
  {
    path: '/microfreak',
    component: MicroFreak
  }
]

const router = new VueRouter({
  routes
})

export default router
